import { Injectable } from '@angular/core';
import { HttpClient ,HttpHeaders} from '@angular/common/http';
import {registration} from './reginterface';
import { wishlist } from './wishlistinterface';
import {Observable} from 'rxjs';
import { listing } from './addlistinterface';

@Injectable({
  providedIn: 'root'
})
export class BookserviceService {
  sellerid:any;
  userid:any;
  product:wishlist={
    Bookname: '',
    Booksellerid: '',
    useridEmail:''
  };
  constructor(private http: HttpClient) { }
  postdata(data){
    this.http.post('/api/users',data).subscribe((data:any)=>{
      console.log(data);
    });
  }
  postwishlist(data){
    this.http.post('/api/wishlist',data).subscribe((data:any)=>{
      console.log(data);
    });
  }
  listpostdata(data){
    this.http.post('/api/listings',data).subscribe((data:any)=>{
      console.log(data);
    });
  }
  
  messagepost(data){
    this.http.post('/api/message',data).subscribe((data:any)=>{
      console.log(data);
    });
  }

  
  getbook():Observable<listing[]>{
    return this.http.get<listing[]>('/api/listings');
  }
  getlist():Observable<registration[]>{
    return this.http.get<registration[]>('/api/users');
  }
  /*getwishlist():Observable<wishlist[]>{
    return this.http.get<wishlist[]>('http://localhost:2678/api/wishlist');
  }*/
}
