import { Routes } from "@angular/router";
import { UserComponent } from "./user/user.component";
import { HomeComponent } from "./user/home/home.component";
import { RegisterationComponent } from "./user/registeration/registeration.component";
import { LoginComponent } from "./user/login/login.component";
import { LoginuserComponent } from "./loginuser/loginuser.component";
import { LoginhomeComponent } from "./loginuser/loginhome/loginhome.component";
import { AddlistingComponent } from "./loginuser/addlisting/addlisting.component";
import { WishlistComponent } from "./loginuser/wishlist/wishlist.component";
import { BookdetailComponent } from "./loginuser/bookdetail/bookdetail.component";
import { MessagesComponent } from "./loginuser/messages/messages.component";


export const appRoutes : Routes =[
    {
        path:'Home', component:UserComponent,
      children:[{path:'', component:HomeComponent}]
    },
    {
      path:'Registration', component:UserComponent,
    children:[{path:'', component:RegisterationComponent}]
  },
  {
    path:'Login', component:UserComponent,
    children:[{path:'', component:LoginComponent}]
  },
  {
    path:'UserHome/:id', component:LoginuserComponent,
  children:[{path:'', component:LoginhomeComponent}]
  },
  {
    path:'AddListing/:id', component:LoginuserComponent,
    children:[{path:'', component:AddlistingComponent}]
  },
  {
    path:'Messages/:id', component:LoginuserComponent,
    children:[{path:'', component:MessagesComponent}]
  },
  {
    path:'WishList/:id', component:LoginuserComponent,
    children:[{path:'', component:WishlistComponent}]
  },
  {
    path:'Detail/:id', component:LoginuserComponent,
    children:[{path:'', component:BookdetailComponent}]
  },
  {
    path:'', redirectTo:'/Home' , pathMatch: 'full'
  },
]