import { Component, OnInit } from '@angular/core';
import { BookserviceService } from '../../bookservice.service';
import { HttpClient ,HttpHeaders} from '@angular/common/http';
import { message } from '../../wishlistinterface';

@Component({
  selector: 'app-messages',
  templateUrl: './messages.component.html',
  styleUrls: ['./messages.component.css']
})
export class MessagesComponent implements OnInit {

  Cuserid:string;
  message:message[]=[];

  constructor(private serv:BookserviceService,private http:HttpClient) { }

  ngOnInit() {
    this.Cuserid=this.serv.userid;
    this.http.post('/api/message/getmessage',{Booksellerid:this.Cuserid}).subscribe((data:any)=>{
        this. message=data;
        //console.log( this. message); 
  })
  }

}
