import { Component, OnInit } from '@angular/core';
import { ActivatedRoute,ParamMap, Router } from '@angular/router';
import { BookserviceService } from '../../bookservice.service';
import { listing } from '../../addlistinterface';
import { HttpClient ,HttpHeaders} from '@angular/common/http';
import { message } from '../../wishlistinterface';

@Component({
  selector: 'app-bookdetail',
  templateUrl: './bookdetail.component.html',
  styleUrls: ['./bookdetail.component.css']
})
export class BookdetailComponent implements OnInit {

 
  constructor(private serv:BookserviceService,private router: ActivatedRoute,private http:HttpClient) { }
  bookId:any;
  Cuserid:string;
  smessage:string;
  bookinfo:listing[]=[];
 
  message:message={
    Message: '',
    Booksellerid: '',
    useridEmail:''
  } 
  addmessage(){
    this.message=({
      Message:this.smessage,
      Booksellerid:this.bookinfo[0].sellerEmail,
      useridEmail:this. Cuserid
    })
    
    const values=this.message;
    this.serv.messagepost(values);
  } 

  ngOnInit() {
    
    this.router.paramMap.subscribe( (params: ParamMap) =>{
      this.bookId = (params.get('id'));
      this.Cuserid=this.serv.userid;
    });
      
      this.http.post('/api/listings/bookdetail',{id:this.bookId}).subscribe((data:any)=>{
        this. bookinfo=data;
        
      
      
  })

  
}

}
