import { Component, OnInit, OnDestroy } from '@angular/core';
import { BookserviceService } from '../../bookservice.service';
import { registration } from '../../reginterface';
import { Router } from '@angular/router';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit ,OnDestroy {

  constructor(private serv:BookserviceService,private route:Router) { }
  listitem:registration[]=[];
  email='';
  password='';
  getItems(){
    this.serv.getlist().subscribe(Prod=>{this.listitem = Prod;
    //console.log(this.listitem);
  })
  }
  
  matchdata(){
    let c=0;
      for(let i=0;i<this.listitem.length;i++)
      {
        if(this.email==this.listitem[i].email && this.password==this.listitem[i].password)
        {
           c=1;
           break;
        }
      }
      if(c==0){
        console.log("not valid");
      }else{
        this.route.navigate(['/UserHome',this.email]);
      }
  }
  ngOnInit() {
    this.getItems();
  }
  ngOnDestroy(){
    this.serv.sellerid=this.email;
  }

}
