import { Component, OnInit,OnDestroy } from '@angular/core';
import { BookserviceService } from '../../bookservice.service';
import {listing } from '../../addlistinterface';
import {wishlist } from '../../wishlistinterface';
import { ActivatedRoute ,ParamMap, Router} from '@angular/router';
import { HttpClient ,HttpHeaders} from '@angular/common/http';

@Component({
  selector: 'app-loginhome',
  templateUrl: './loginhome.component.html',
  styleUrls: ['./loginhome.component.css']
})
export class LoginhomeComponent implements OnInit ,OnDestroy {
  
 

  constructor(private serv:BookserviceService,private router: ActivatedRoute,private route:Router,private http:HttpClient) { }
  filter:string;
  fvalue:any;
  userId:string;
  wishlists:wishlist={
    Bookname: '',
    Booksellerid: '',
    useridEmail:''
  }
  bookinfo:listing[]=[];
    Bookname='';
    author='';
    discription='';
    price=0;
    condition='';
    sellerEmail='';
  
    getbookinfo(){
      this.serv.getbook().subscribe(Prod=>{this.bookinfo = Prod;
        
    })
  }
  rotdeatil(id){
    this.route.navigate(['/Detail',id]);
  }
  addtowish(index){
    this.wishlists=({
      Bookname:this.bookinfo[index].Bookname,
      Booksellerid:this.bookinfo[index].sellerEmail,
      useridEmail:this. userId
    });
    
    const values=this.wishlists;
    this.serv.postwishlist(values);
  }

  filterb(){
    
    if(this.filter== 'condition')
    {
      this.http.post('/api/listings/filterbycondition',{condition:this.fvalue}).subscribe((data:any)=>{
        this. bookinfo=data;
       // console.log( this. bookinfo);
    })
  }
  if(this.filter== 'name')
    {
      this.http.post('/api/listings/filterbyname',{Bookname:this.fvalue}).subscribe((data:any)=>{
        this. bookinfo=data;
        //console.log( this. bookinfo);
    })
  }
  if(this.filter== 'Author')
    {
      this.http.post('/api/listings/filterbyauthor',{author:this.fvalue}).subscribe((data:any)=>{
        this. bookinfo=data;
        //console.log( this. bookinfo);
    })
  }

}

  ngOnInit() {
    
this.getbookinfo();
this.router.paramMap.subscribe( (params: ParamMap) =>{
  this.userId = (params.get('id'));
 // console.log(this.userId);
  
});
  }

   
  ngOnDestroy(){
    this.serv.userid=this.userId;
  }

}
